@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1b6;
  border: 1px solid rgba(0, 0, 0, 0.4);
}

::-webkit-scrollbar-thumb {
  background: #f2c94c;
}
.app {
  height: auto;
  width: 100%;
  position: absolute;
  background: #f2994a;
  background: linear-gradient(to right, #f2c94c, #f2994a);
}

.navbar {
  height: 8vh;
  width: 95vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: bisque;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(2.5px);
  -webkit-backdrop-filter: blur(2.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  position: fixed;
  left: 2.5vw;
  border-radius: 10px;
  padding: 10px;
  top: 2%;
  z-index: 100;
}

.main {
  height: auto;
  margin: 0 auto;
  min-height: 100vh;
  padding-top: 10vh;
}

.logo-container {
  flex: 2 1;
}
.logo-container h1 {
  font-size: 1.8rem;
  color: #14213d;
}
.nav-links {
  flex: 1 1;
  display: flex;
  justify-content: space-around;
  list-style: none;
}
.nav-link {
  text-decoration: none;
  color: #1d3557;
  font-weight: 400;
}

.home__container {
  margin-top: 10%;
  display: flex;
  justify-content: center;
  padding: 0rem 3rem;
}
.home__text__content {
  width: 50%;
  flex: 1 1;
  padding: 0 8% 0 2%;
  color: #004281;
}
.home__text__content h1 {
  font-size: 3rem;
  font-weight: 600;
  margin: 1rem auto;
  letter-spacing: 2px;
}
.home__text__content h2 {
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 2px;
}
.home__text__content button {
  margin: 2rem 0;
  padding: 1rem 2rem;
  font-size: 1rem;
  background: transparent;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  color: #004281;
  transition: 0.6s;
}
.home__text__content button:hover {
  background: #004281;
  color: aliceblue;
  transform: translateY(-5px) scaleY(1.1);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4);
}

.home__image__content {
  width: 50%;
}
.home__image {
  flex: 1 1;
  display: grid;
  place-items: center;
  width: 100%;
}

.footer {
  background-color: #001529d8;
  display: flex;
  padding: 20px;
  align-items: center;
  color: aliceblue;
  width: 100%;
}
.footer__logo__container {
  font-size: 1rem;
  flex: 2 1;
}
.footer__logo__container h1 {
  color: aliceblue;
}
.social-links {
  flex: 1 1;
}
.social-links ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
}
.social-links ul li a {
  text-decoration: none;
  color: aliceblue;
}

.loginPage-container {
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;

  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url(https://source.unsplash.com/1600x900/?nature,water);
  background-position: center;
  background-size: cover;
  font-family: "Roboto Mono", monospace;
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 3rem;
}
.form-box {
  min-width: 420px;
  width: 40%;
  height: 60%;
  min-height: 480px;
  position: relative;

  background: #ffffffe6;
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.form-box h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}
.input-group {
  width: 280px;
  transition: 0.5s;
  font-size: 1rem;
}
.input-field {
  width: 100%;
  padding: 10px 0;
  margin: 5px 0;
  border-left: 0;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid #999;
  outline: none;
  background: transparent;
  font-size: 1rem;
}
.submit-btn {
  width: 85%;
  padding: 10px 30px;
  cursor: pointer;
  display: block;
  margin: 0px auto 10px auto;
  background: linear-gradient(to right, #ff105f, #ffad06);
  border: 0;
  outline: none;
  border-radius: 30px;
  font-size: 1rem;
}
.check-box {
  margin: 30px 10px 30px 0;
}
.login-container {
  color: #777;
  font-size: 0.8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login-container span {
  font-size: 0.8rem;
  margin: 0.2rem;
}
.register-container {
  color: #777;
  margin-top: 20px;
  font-size: 0.8rem;
  display: flex;
  justify-content: center;
}

.sell__container {
  width: 100%;
  display: flex;
}
.sell__container__image {
  padding: 5rem 0;
  width: 35%;
  position: absolute;
  bottom: 10%;
}
.sell__image {
  width: 100%;
  -webkit-filter: opacity(0.25);
          filter: opacity(0.25);
}
.sell__container__text {
  z-index: 3;
  margin: 2% 2% 2% 2%;
  width: 100%;
  padding: 3rem 4rem;
  display: flex;
  justify-content: space-around;
}
.sell__container__text form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
td {
  padding: 0rem 0rem 1rem 0rem;
}
.sell__form__label {
  font-size: 1rem;
  color: #222;
  font-weight: 600;
}
.sell__form__input {
  margin: 0.4rem 1rem;
  width: 28ch;
  padding: 2px 8px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #333;
  font-size: 1.2rem;
  outline: none;
}

.sell__form__select {
  margin: 0.4rem 1rem;
  width: 30ch;
  padding: 2px 8px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #333;
  font-size: 1.1rem;
  outline: none;
  color: #777;
}
input[type="file"] {
  display: none;
}

.sell__form__file {
  border: none;
  background-color: royalblue;
  padding: 6px 15px;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  margin: 0rem 1rem;
}
.sell__form__owner h1,
.sell__form__vehicle h1 {
  color: #2f80ed;
  margin-bottom: 2rem;
}

